// app/(auth)/invite/accept/page.tsx
"use client";
import React, { useEffect, useState } from "react";
import { useSearchParams, useRouter } from "next/navigation";

type InvitePayload = {
  ok: boolean;
  email?: string;
  preverifiedEmail?: boolean;
  role?: string | null;
  organizationId?: string | null;
  organizationName?: string | null;
  teamId?: string | null;
  expiresAt?: string | null;
  createdAt?: string | null;
  message?: string;
};

export default function InviteAcceptPage() {
  const searchParams = useSearchParams();
  const router = useRouter();
  const token = searchParams.get("token") ?? "";

  const [loading, setLoading] = useState<boolean>(true);
  const [invite, setInvite] = useState<InvitePayload | null>(null);
  const [error, setError] = useState<string | null>(null);

  // form state
  const [name, setName] = useState("");
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");

  // phone verification state
  const [smsSending, setSmsSending] = useState(false);
  const [smsSent, setSmsSent] = useState(false);
  const [smsCode, setSmsCode] = useState("");
  const [smsVerifying, setSmsVerifying] = useState(false);
  const [phoneVerified, setPhoneVerified] = useState(false);

  // submission
  const [submitting, setSubmitting] = useState(false);

  // auth check (to warn if a logged-in user opens an invite)
  const [currentUserEmail, setCurrentUserEmail] = useState<string | null>(null);
  const [checkingAuth, setCheckingAuth] = useState(true);

  useEffect(() => {
    // If token missing, short-circuit
    if (!token) {
      setError("Missing invite token");
      setLoading(false);
      setCheckingAuth(false);
      return;
    }

    // fetch invite metadata
    setLoading(true);
    fetch("/api/invites/accept", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ token }),
    })
      .then((r) => r.json())
      .then((json: InvitePayload) => {
        if (!json || !json.ok) {
          throw new Error(json?.message || "Invalid invite");
        }
        setInvite(json);
      })
      .catch((err: any) => {
        console.error("Invite accept error:", err);
        setError(err?.message || "Could not load invite");
      })
      .finally(() => setLoading(false));

    // check /api/me to detect if visitor is already signed in (so we can warn)
    setCheckingAuth(true);
    fetch("/api/me")
      .then((r) => {
        if (!r.ok) return null;
        return r.json();
      })
      .then((json: any) => {
        if (json && json?.email) setCurrentUserEmail(String(json.email));
      })
      .catch(() => {
        /* ignore */
      })
      .finally(() => setCheckingAuth(false));
  }, [token]);

  // send SMS code
  async function handleSendSms() {
    if (!phone) {
      setError("Please enter phone number to receive SMS");
      return;
    }
    setError(null);
    setSmsSending(true);
    try {
      const res = await fetch("/api/auth/phone/send", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ phone }),
      });
      const json = await res.json();
      if (!json?.ok) {
        throw new Error(json?.message || "Failed to send SMS");
      }
      setSmsSent(true);
    } catch (err: any) {
      console.error("SMS send error:", err);
      setError(err?.message || "Failed to send SMS");
    } finally {
      setSmsSending(false);
    }
  }

  // verify SMS code
  async function handleVerifySms() {
    if (!smsCode) {
      setError("Enter the code you received by SMS");
      return;
    }
    setError(null);
    setSmsVerifying(true);
    try {
      const res = await fetch("/api/auth/phone/verify", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ phone, code: smsCode }),
      });
      const json = await res.json();
      if (!json?.ok) throw new Error(json?.message || "Verification failed");
      setPhoneVerified(true);
      setError(null);
    } catch (err: any) {
      console.error("SMS verify error:", err);
      setError(err?.message || "Failed to verify code");
    } finally {
      setSmsVerifying(false);
    }
  }

  // finalize invite -> create account
  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);

    if (!invite?.email) {
      setError("Invite data missing");
      return;
    }
    if (!password || password.length < 8) {
      setError("Password must be at least 8 characters");
      return;
    }

    // basic name validation (optional)
    if (name && name.length > 100) {
      setError("Name is too long");
      return;
    }

    setSubmitting(true);
    try {
      const payload: any = {
        token,
        name: name || undefined,
        password,
      };
      if (phone) {
        payload.phone = phone;
        // only tell server that phone was verified if we actually verified it via endpoint
        if (phoneVerified) payload.phoneVerified = true;
      }

      const res = await fetch("/api/invites/finalize", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });
      const json = await res.json();
      if (!json?.ok) {
        throw new Error(json?.message || "Failed to create account");
      }

      // Success: redirect to login or welcome screen
      // We intentionally don't auto-show token. We can show a friendly page instead.
      router.push("/auth/login?created=1");
    } catch (err: any) {
      console.error("Finalize error:", err);
      setError(err?.message || "Failed to create account");
    } finally {
      setSubmitting(false);
    }
  }

  if (loading || checkingAuth) {
    return <div style={{ padding: 24 }}>Loading…</div>;
  }

  if (error) {
    return (
      <div style={{ padding: 24 }}>
        <h1>Invite</h1>
        <p style={{ color: "red" }}>{error}</p>
        <p>
          If you think this is a mistake, contact the organization that invited you or try requesting a
          new invite.
        </p>
      </div>
    );
  }

  if (!invite) {
    return (
      <div style={{ padding: 24 }}>
        <h1>Invite</h1>
        <p>Invite not found or invalid.</p>
      </div>
    );
  }

  // If a user is already signed in, show a warning and suggest using a private window to accept
  const showSignedInWarning = !!currentUserEmail;

  return (
    <div style={{ maxWidth: 720, margin: "24px auto", padding: 16 }}>
      <h1 style={{ marginBottom: 8 }}>You're invited to join</h1>

      <div style={{ padding: 12, borderRadius: 8, background: "#f8fafc", marginBottom: 18 }}>
        <div style={{ fontSize: 18, fontWeight: 600 }}>
          {invite.organizationName ?? "the organization"}
        </div>
        <div style={{ marginTop: 6 }}>
          Invite for: <strong>{invite.email}</strong>{" "}
          {invite.preverifiedEmail ? (
            <span style={{ color: "green", marginLeft: 8 }}>✔ email verified by invite</span>
          ) : null}
        </div>
        {invite.role ? <div style={{ marginTop: 6 }}>Role: <strong>{invite.role}</strong></div> : null}
        {invite.expiresAt ? <div style={{ marginTop: 6, fontSize: 12 }}>Expires: {new Date(invite.expiresAt).toLocaleString()}</div> : null}
      </div>

      {showSignedInWarning ? (
        <div style={{ padding: 12, border: "1px solid #f1c40f", background: "#fff7e6", borderRadius: 8, marginBottom: 16 }}>
          <strong>Warning:</strong> You're currently signed in as <strong>{currentUserEmail}</strong>. To accept this invite
          as a different account, sign out first or open the invite link in a private/incognito window.
        </div>
      ) : null}

      <form onSubmit={handleSubmit} style={{ display: "grid", gap: 12 }}>
        {/* Email (locked) */}
        <label style={{ display: "block" }}>
          Email (locked)
          <input value={invite.email} readOnly style={{ width: "100%", padding: 10, marginTop: 6 }} />
        </label>

        {/* Name */}
        <label style={{ display: "block" }}>
          Full name
          <input
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder="Your full name"
            style={{ width: "100%", padding: 10, marginTop: 6 }}
          />
        </label>

        {/* Phone + SMS verify */}
        <div style={{ display: "grid", gap: 8 }}>
          <label>
            Phone (optional)
            <input
              value={phone}
              onChange={(e) => {
                setPhone(e.target.value);
                setSmsSent(false);
                setPhoneVerified(false);
              }}
              placeholder="+91xxxxxxxxxx"
              style={{ width: "100%", padding: 10, marginTop: 6 }}
            />
          </label>

          <div style={{ display: "flex", gap: 8, alignItems: "center" }}>
            <button
              type="button"
              onClick={handleSendSms}
              disabled={!phone || smsSending}
              style={{ padding: "8px 12px" }}
            >
              {smsSending ? "Sending…" : smsSent ? "Resend code" : "Send verification code"}
            </button>

            {smsSent && !phoneVerified ? (
              <>
                <input
                  value={smsCode}
                  onChange={(e) => setSmsCode(e.target.value)}
                  placeholder="Enter code"
                  style={{ padding: 8 }}
                />
                <button type="button" onClick={handleVerifySms} disabled={smsVerifying} style={{ padding: "8px 12px" }}>
                  {smsVerifying ? "Verifying…" : "Verify"}
                </button>
              </>
            ) : null}

            {phoneVerified ? <span style={{ color: "green" }}>✔ Phone verified</span> : null}
          </div>
        </div>

        {/* Password */}
        <label style={{ display: "block" }}>
          Password
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder="Choose a secure password (8+ characters)"
            style={{ width: "100%", padding: 10, marginTop: 6 }}
          />
        </label>

        {error ? <div style={{ color: "red" }}>{error}</div> : null}

        <div style={{ display: "flex", gap: 8, alignItems: "center" }}>
          <button type="submit" disabled={submitting} style={{ padding: "10px 14px" }}>
            {submitting ? "Creating account…" : `Create account and join ${invite.organizationName ?? "team"}`}
          </button>

          <button
            type="button"
            onClick={() => router.push("/auth/login")}
            style={{ padding: "10px 14px", background: "transparent", border: "1px solid #ddd" }}
          >
            Already have an account? Log in
          </button>
        </div>
      </form>
    </div>
  );
}
